import React, { Component } from 'react';

class Nav extends Component {
    state ={
        toggle: false
    }
    menuToggle = () => {
      this.setState({toggle: !this.state.toggle})
    }
    render() {
        const {toggle} = this.state
        return (
            <>
                <ul className={toggle ? "toggle" : ""}>
                    <li onClick={this.menuToggle}> <a href="#Petites informations personnelles">Information</a></li>
                    <li onClick={this.menuToggle}> <a href="#timeline">Timeline</a></li>
                    <li onClick={this.menuToggle}> <a href="#competence">Competences / outils</a></li>
                    <li onClick={this.menuToggle}> <a href="#contact">Contact</a></li>
                    <li className="close" onClick={this.menuToggle}>X</li>
                </ul>
                <div className="menu" onClick={this.menuToggle}>Menu</div>
            </>
        );
    }
}

export default Nav;
